.section-new-history {
    display: flex;
    flex: 3;
    flex-direction: column;
    color: #1F1B16;
    width: 100%;
    box-sizing: border-box;
    padding: 9px 120px 70px 130px;
    background: url('../../images/DefaultBG.jpg') no-repeat center center;
    background-size: cover;
}

.header-container-new-history {
    border-bottom: 2px solid #1F1B16;
    margin-bottom: 20px;
}

.header-of-section {
    color: #1F1B16;
    font-family: 'Oswald', sans-serif;
    font-size: 50px;
    font-weight: 400;
    text-align: center;
}

.container-form-new-history {
    /* display: flex;
    flex-direction: row;
    gap: 60px;
    padding: 20px 138px 37px 141px; */
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 88px 37px;

    
}

.section-form-new-history {

    justify-content: space-between;
}

.container-inputs-form-new-history {
    /* display: flex;
    flex-direction: column;
    gap: 20px;
    margin-right: 80px; */
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 22px;
}

.container-story-form-new-history {
    flex: 1;
}

.section-register-new-history {
    padding: 65px 88px 0px;
    border-top: 2px solid #1F1B16;
    display: flex;
    flex-direction: row;
    align-items: start;

}

.container-register-header-new-history {
    text-align: center;
    margin-right: 73px;
    margin-top: 17px;
}

.container-register-header-new-history h2 {
    font-family: 'Zen Kurenaido';
    font-weight: 400;
    font-size: 40px;
    color: #1F1B16;
}

.container-register-header-new-history span {
    font-family: 'Zen Kurenaido';
    font-weight: 400;
    font-size: 25px;
    color: #1F1B16;
    opacity: 80%;
}

.container-register-form-new-history {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 400px;
    align-items: center;
}

.container-register-form-inputs-new-history {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    max-width: 400px;
    align-items: center;

}

.container-register-form-button-new-history{
    margin-top: 36px;
}




/* .container {
    width: 80%;
    margin: 50px auto;
} */

.date-range {
    width: 100%;
    margin-top: -6px;
    display: flex;
    justify-content: flex-end;
    gap: 20px;
}

.date-range label {
    font-family: "Zen Kurenaido", sans-serif;
    font-size: 22px;
    color: #1F1B16;
}

.date-range input {
    width: 30% !important;
    height: 30px !important;
}

