.container-description-archive {
    padding-bottom: 6px;
    padding-left: 14px;
    padding-bottom: 19px;
    margin-bottom: 32px;
    border-bottom: 2px solid #1F1B16;
    font-size: 22px;
}

.active-place {
    width: 550px;
    height: 990px;
    background-color: #4D4B48;
    opacity: 90;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.container-active-place {
    display: flex;
    flex-direction: column;
    height: 100%;
    margin: 0px 25px 64px;
    border: 3px solid #E2D4C4;
    border-radius: 10px;
    z-index: 2;
}

.header-active-place {
    display: flex;
    justify-content: flex-end;
    padding: 30px;
    z-index: 2;
    border-radius: 10px;
    background-color: #4D4B48;
}

.header-active-place img {
    width: 40px;
    height: 40px;
    cursor: pointer;
    transition: transform 0.3s ease;
    
}

.header-active-place img:hover {
    
    transform: scale(1.1);
}

.content-active-place {
    flex: 1;
    display: flex;
    flex-direction: column;
    background-color: #4D4B48;
    border-radius: 10px;
    
}

.container-image-active-place {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #E2D4C4;
    border-radius: 10px;
    
}

.container-image-active-place img {
    width: 100%;
    height: auto;
    max-height: 300px;
    object-fit: cover;
    box-shadow: 0px 3px 8px black;
    z-index: 0;
    border-radius: 7px;
}

.container-text-active-place {
    flex: 1;
    padding: 20px;
    background-color: #453F38;
    color: #E2D4C4;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    border-radius: 10px;
}

.container-text-active-place h2 {
    color: #E4B474;
    margin-block-start: 0em;
    margin-block-end: 0em;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
}

.container-text-active-place ul {
    list-style: none;
    padding: 0;
    margin: 0;
    font-family: "Inter", sans-serif;
}

.container-text-active-place li {
    display: flex;
    align-items: center;
}

.container-text-active-place h3 {
    color: #E2D4C4;
    margin: 0;
    font-size: 1.2em;
}

.container-text-active-place span {
    color: #E2D4C4;
    font-size: 18px;
    padding-left: 10px;
    padding-top: 1px;
        
}