.header {
    position: absolute;
    top: 0;
    width: 100%;
    height: 140px;
    background: linear-gradient(to bottom, rgba(10, 10, 10, 0.8) 0%, rgba(46, 42, 36, 0.6) 61%, rgba(118, 105, 89, 0) 98%);
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 20px 130px;
    box-sizing: border-box;
  }
  
  .header-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .links {
    display: flex;
    gap: 53px;
  }
  
  .link {
    color: white;
    text-decoration: none;
    transition: color 0.3s ease;
    font-family: 'Zen Kurenaido';
    font-weight: 400;
    font-size: 26px;
  }
  
  .link:hover {
    color: #E4B474;
  }
  
  .globus-button {
    display: flex;
    align-items: center;
  }
  
  .globus-button svg {
    width: 40px;
    height: 40px;
    cursor: pointer;
    transition: filter 0.3s ease;
    color: #E4B474;
  }
  
  .language-menu {
    width: 140px;
    height: 120px;
    background-color: #453F38;
    border-radius: 10px;
    padding: 8px;
    padding-top: 10px;
    padding-left: 12px;
    box-sizing: border-box;
    position: absolute;
    top: 110px;
    left: 93.5%;
    transform: translateX(-50%);
    z-index: 1000;
  }
  
  .language-menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
    color: #EFE3D4;
  }
  
  .language-menu li {
    font-family: 'Zen Kurenaido', sans-serif;
    font-size: 18px;
    margin-bottom: 10px;
    transition: color 0.3s ease;
    position: relative;
    padding-left: 15px;
    cursor: pointer;
  }
  
  .language-menu li.active {
    color: #E4B474;
  }
  
  .language-menu li.active::before {
    content: '';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 5px;
    height: 5px;
    background-color: #E4B474;
    border-radius: 50%;
  }
  
  .language-menu li:hover {
    color: #E4B474;
  }