.carousel-container {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 300px;
    margin: auto;
    cursor: grab;
    background: #1F1B16;
    padding:49px 0px 21px 23px;
  }

  .carousel-container h4 {
    font-size: 20px;
    position: absolute;
    color: #E9C79A;
    top:-18px;
  }
  
  .carousel {
    display: flex;
    transition: transform 0.5s ease;
    scroll-snap-type: x mandatory;
    overflow-x: scroll;
    scrollbar-width: none; /* For Firefox */
    -ms-overflow-style: none;  /* For Internet Explorer and Edge */
  }
  
  .carousel::-webkit-scrollbar {
    display: none; /* For Chrome, Safari, and Opera */
  }
  
  .carousel-item {
    flex: none;
    width: 347px;
    height: 230px;
    margin-right: 10px;
  }
  
  .carousel-item img {
    width: 347px;
    height: 230px;
    object-fit: cover;
  }
  